import "./newUser.css"
import { Link } from 'react-router-dom';
import { useNavigate } from 'react-router-dom';
import { userRows } from "../../dummyData";
import { useState } from 'react'




export default function NewUser() {

  const [id, setId] = useState('')
  const [username, setUsername] = useState('')
  const [fullname, setFullname] = useState('')
  const [avatar, setAvatar] = useState('')
  const [email, setEmail] = useState('')
  const [address, setAddress] = useState('')
  const [phone, setPhone] = useState('')
  const [status, setStatus] = useState('')
  const [transaction, setTransaction] = useState('')

  let history = useNavigate()
  const handleUser = (e) => {
    e.preventDefault()
    userRows.push({
      id: id,
      username: username,
      fullname: fullname,
      avatar: avatar,
      email: email,
      address: address,
      phone: phone,
      status: status,
      transaction: transaction
    })
    history('/users')
  }

  return (
    <div className="newUser">
      <h1 className="newUserTitle">New User</h1>
      <form className="newUserForm">
        <div className="newUserItem">
          <label>User Id</label>
          <input type="text" onChange={(e) => setId(e.target.value)} />
        </div>
        <div className="newUserItem">
          <label>Image</label>
          <input type="text" onChange={(e) => setAvatar(e.target.value)} placeholder="URL" />
        </div>

        <div className="newUserItem">
          <label>Username</label>
          <input type="text" onChange={(e) => setUsername(e.target.value)} />
        </div>
        <div className="newUserItem">
          <label>Full Name</label>
          <input type="text" onChange={(e) => setFullname(e.target.value)} />
        </div>
        <div className="newUserItem">
          <label>Address</label>
          <input type="text" onChange={(e) => setAddress(e.target.value)} />
        </div>
        <div className="newUserItem">
          <label>Email</label>
          <input type="email" onChange={(e) => setEmail(e.target.value)}  />
        </div>
        <div className="newUserItem">
          <label>Phone</label>
          <input type="text" onChange={(e) => setPhone(e.target.value)} />
        </div>

        <div className="newUserItem">
          <label>Transaction</label>
          <input type="text" onChange={(e) => setTransaction(e.target.value)} />
        </div>
        <div className="newUserItem">
          <label>Status</label>
          <input type="text" onChange={(e) => setStatus(e.target.value)} placeholder="Active / Non-Active" />
        </div>

        <Link to="/users">
          <button onClick={(e) => handleUser(e)} className="newUserButton">Create</button>
        </Link>

      </form>
    </div>
  );
}
