import "./newProduct.css"
import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom';
import { productRows } from "../../dummyData";



function NewProduct() {

  const [id, setId] = useState('')
  const [name, setName] = useState('')
  const [img, setImg] = useState('')
  const [stock, setStock] = useState('')
  const [status, setStatus] = useState('')
  const [price, setPrice] = useState('')

  let history=useNavigate()
    const handleProduct=(e)=>{
      e.preventDefault()
      productRows.push({
        id:id,
        name:name,
        img:img,
        stock:stock,
        status:status,
        price:price
      })
      history('/products')
    }

  return (
    <div className="newProduct">
      <h1 className="addProductTitle">New Product</h1>
      <form className="addProductForm">
        <div className="addProductItem">
          <label>Image</label>
          <input onChange={(e) => setImg(e.target.value)} type="file" id="file" />
        </div>
        <div className="addProductItem">
          <label>Product Id</label>
          <input onChange={(e) => setId(e.target.value)} type="text"  />
        </div>
        <div className="addProductItem">
          <label>Name</label>
          <input onChange={(e) => setName(e.target.value)} type="text"  />
        </div>
        <div className="addProductItem">
          <label>Stock</label>
          <input onChange={(e) => setStock(e.target.value)} type="text" />
        </div>
        <div className="addProductItem">
          <label>Active</label>
          <input onChange={(e) => setStatus(e.target.value)} type="text" />
        </div>
        <div className="addProductItem">
          <label>Price</label>
          <input onChange={(e) => setPrice(e.target.value)} type="text" />
        </div>
        <button onClick={(e)=>handleProduct(e)} className="addProductButton">Create</button>
      </form>
    </div>
  );
}

export default NewProduct
