import { CalendarToday, LocationSearching, MailOutline, PermIdentity, PhoneAndroid, Publish, } from "@material-ui/icons";
import { Link } from "react-router-dom";
import "./user.css";
import React, { useEffect, useState } from 'react'
import { userRows } from "../../dummyData";
import { useNavigate } from 'react-router-dom';




export default function User() {

  const [id, setId] = useState('')
  const [username, setUsername] = useState('')
  const [fullname, setFullname] = useState('')
  const [avatar, setAvatar] = useState('')
  const [email, setEmail] = useState('')
  const [phone, setPhone] = useState('')
  const [address, setAddress] = useState('')
  const [status, setStatus] = useState('')
  const [transaction, setTransaction] = useState('')

  useEffect(() => {
    setId(localStorage.getItem("id"))
    setUsername(localStorage.getItem("username"))
    setFullname(localStorage.getItem("fullname"))
    setAvatar(localStorage.getItem("avatar"))
    setEmail(localStorage.getItem("email"))
    setPhone(localStorage.getItem("phone"))
    setAddress(localStorage.getItem("address"))
    setStatus(localStorage.getItem("status"))
    setTransaction(localStorage.getItem("transaction"))
  }, [])

  var index = userRows.map(item=>item.id).indexOf(id)
  console.log(index);
  let history = useNavigate()
  const handleUpdate = (e) => {
    e.preventDefault()
    let user = userRows[index]
    // console.log(user);
    user.id = id
    user.username = username
    user.fullname = fullname
    user.avatar = avatar
    user.email = email
    user.phone = phone
    user.address = address
    user.status = status
    user.transaction = transaction
    history('/users')
  }

  return (
    <div className="user">
      <div className="userTitleContainer">
        <h1 className="userTitle">Edit User</h1>
        <Link to="/newUser">
          <button className="userAddButton">Create</button>
        </Link>
      </div>
      <div className="userContainer">
        <div className="userShow">
          <div className="userShowTop">
            <img src={avatar} alt="" className="userShowImg"  />
            <div className="userShowTopTitle">
              <span className="userShowUsername">{username}</span>
              {/* <span className="userShowUserTitle">Software Engineer</span> */}
            </div>
          </div>
          <div className="userShowBottom">
            <span className="userShowTitle">Account Details</span>
            <div className="userShowInfo">
              <PermIdentity className="userShowIcon" />
              <span className="userShowInfoTitle">{fullname}</span>
            </div>
            <div className="userShowInfo">
              <CalendarToday className="userShowIcon" />
              <span className="userShowInfoTitle">{status}</span>
            </div>
            <span className="userShowTitle">Contact Details</span>
            <div className="userShowInfo">
              <PhoneAndroid className="userShowIcon" />
              <span className="userShowInfoTitle">{phone}</span>
            </div>
            <div className="userShowInfo">
              <MailOutline className="userShowIcon" />
              <span className="userShowInfoTitle">{email}</span>
            </div>
            <div className="userShowInfo">
              <LocationSearching className="userShowIcon" />
              <span className="userShowInfoTitle">{address}</span>
            </div>
          </div>
        </div>
        <div className="userUpdate">
          <span className="userUpdateTitle">Edit</span>
          <form className="userUpdateForm">
            <div className="userUpdateLeft">
              <div className="userUpdateItem">
                <label>Username</label>
                <input
                  type="text"
                  placeholder={username}
                  className="userUpdateInput"
                  onChange={(e) => setUsername(e.target.value)}
                />
              </div>
              <div className="userUpdateItem">
                <label>Full Name</label>
                <input
                  type="text"
                  placeholder={fullname}
                  className="userUpdateInput"
                  onChange={(e) => setFullname(e.target.value)}

                />
              </div>
              <div className="userUpdateItem">
                <label>Email</label>
                <input
                  type="text"
                  placeholder={email}
                  className="userUpdateInput"
                  onChange={(e) => setEmail(e.target.value)}

                />
              </div>
              <div className="userUpdateItem">
                <label>Phone</label>
                <input
                  type="text"
                  placeholder={phone}
                  className="userUpdateInput"
                  onChange={(e) => setPhone(e.target.value)}

                />
              </div>
              <div className="userUpdateItem">
                <label>Address</label>
                <input
                  type="text"
                  placeholder={address}
                  className="userUpdateInput"
                  onChange={(e) => setAddress(e.target.value)}

                />
              </div>
              <div className="userUpdateItem">
                <label>Status</label>
                <input
                  type="text"
                  placeholder={status}
                  className="userUpdateInput"
                />
              </div>
            </div>
            {/* <button onClick={(e)=>handleUpdate(e)} type="update" >Update</button> */}

            <div className="userUpdateRight">
              <div className="userUpdateUpload">
                <img
                  className="userUpdateImg"
                  src={avatar}
                  alt=""
                />
                <label htmlFor="file">
                  <Publish className="userUpdateIcon" />
                </label>
                {/* <input type="file" id="file" style={{ display: "none" }} /> */}
              </div>
              <button onClick={(e) => handleUpdate(e)} className="userUpdateButton" type="update" >Update</button>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
}