import "./userList.css";
import { userRows } from "../../dummyData";
import { Link, useNavigate } from 'react-router-dom';
import { DeleteOutline } from "@material-ui/icons";
import { Button, Table } from "react-bootstrap";

 
export default function UserList() {

  const history = useNavigate()

  const handleDelete = (id) => {
    let index = userRows.map(item => item.id).indexOf(id)
    userRows.splice(index, 1)
    history('/users')
  };


  const handleEdit = (id, username,fullname, avatar, email,phone,address, status, transaction) => {
    localStorage.setItem("id", id)
    localStorage.setItem("username", username)
    localStorage.setItem("fullname", fullname)
    localStorage.setItem("avatar", avatar)
    localStorage.setItem("email", email)
    localStorage.setItem("phone", phone)
    localStorage.setItem("address", address)
    localStorage.setItem("status", status)
    localStorage.setItem("transaction", transaction)
  }


  return (

    <div className="table" style={{ margin: "4rem" }}>
      
      <div className='create'>
        <Link to={'/newUser'}>
        <Button className="userCreate" variant="primary">Create</Button>
        </Link>
      </div>

      <Table className="tableborder">
        <thead>
          <tr>
            <th>Id</th>
            <th>Username</th>
            <th>Email</th>
            <th>Status</th>
            <th>Actions</th>
          </tr>
        </thead>
        <tbody className="tbody">
          {
            userRows && userRows.length > 0 ?
              userRows.map((item) => (
                <tr>
                  <td> {item.id} </td>
                  <td>
                  <div className="productListItem">
                      <img className="productListImg" src={item.avatar} alt="" />
                      {item.username}
                    </div>
                      </td>
                  <td> {item.email} </td>
                  <td> {item.status} </td>
                  <td>
                    <Link to={'/user/' + item.id}>
                      <Button className="userListEdit" onClick={() => handleEdit(item.id, item.username,item.fullname,item.avatar, item.email,item.phone,item.address, item.status)} variant="primary">Edit</Button>
                    </Link>
                    <DeleteOutline className="userListDelete" onClick={() => handleDelete(item.id)} />
                  </td>
                </tr>
              ))
              : 'no table data available'
          }
        </tbody>
      </Table>
      
    </div>
  )
}































