import "./productList.css"
import { Link, useNavigate } from "react-router-dom";
// import { useState } from "react";
import { productRows } from "../../dummyData";
// import { Button, Table } from "@material-ui/core";
import { DeleteOutline } from "@material-ui/icons";
import { Button, Table } from "react-bootstrap";



function ProductList() {
  const history = useNavigate()

  const handleDelete = (id) => {
    let index = productRows.map(item => item.id).indexOf(id)
    productRows.splice(index, 1)
    history('/products')
  };



  const handleEdit = (id, name, img, stock, status, price) => {
    localStorage.setItem("id", id)
    localStorage.setItem("name", name)
    localStorage.setItem("img", img)
    localStorage.setItem("stock", stock)
    localStorage.setItem("status", status)
    localStorage.setItem("price", price)
  }

  return (

    <div className="table" style={{ margin: "4rem" }}>

      <div className='create'>
        <Link to={'/newproduct'}>
          <Button className="productCreate" variant="primary">Create</Button>
        </Link>
      </div>

      <Table className="tableborder">
        <thead>
          <tr>
            <th>Id</th>
            <th>Name</th>
            <th>Stock</th>
            <th>Status</th>
            <th>Price</th>
            <th>Actions</th>
          </tr>
        </thead>
        <tbody className="tbody">
          {
            productRows && productRows.length > 0 ?
              productRows.map((item) => (
                <tr>
                  <td> {item.id} </td>
                  <td>
                    <div className="imageName">
                      <div className="productListItem">
                        <img className="productListImg" src={item.img} alt="" />
                        {item.name}
                      </div>
                    </div>

                  </td>

                  <td> {item.stock} </td>
                  <td> {item.status} </td>
                  <td> {item.price} </td>
                  <td>
                    <div className="productButtons">
                      <Link to={'/product/' + item.id}>
                        <Button className="productListEdit" onClick={() => handleEdit(item.id, item.name, item.img, item.stock, item.status, item.price)} variant="primary">Edit</Button>
                      </Link>
                      <DeleteOutline className="productListDelete" onClick={() => handleDelete(item.id)} />
                    </div>

                  </td>
                </tr>
              ))
              : 'no table data available'
          }
        </tbody>
      </Table>

    </div>
  )

}

export default ProductList
