import "./product.css"
import { Link } from "react-router-dom";
import Chart from "../../components/chart/Chart"
import {productData, productRows} from "../../dummyData"
import { Publish } from "@material-ui/icons";
import React, { useEffect, useState } from 'react'
import { useNavigate } from 'react-router-dom';


export default function Product() {

    const [id, setId] = useState('')
  const [name, setName] = useState('')
  const [img, setImg] = useState('')
  const [stock, setStock] = useState('')
  const [status, setStatus] = useState('')
  const [price, setPrice] = useState('')

  useEffect(() => {
    setId(localStorage.getItem("id"))
    setName(localStorage.getItem("name"))
    setImg(localStorage.getItem("img"))
    setStock(localStorage.getItem("stock"))
    setStatus(localStorage.getItem("status"))
    setPrice(localStorage.getItem("price"))
  }, [])

  var index = productRows.map(item=>item.id).indexOf(id)
  console.log(index);
  let history = useNavigate()
  const handleUpdate = (e) => {
    e.preventDefault()
    let user = productRows[index]
    user.id = id
    user.name = name
    user.img = img
    user.status = status
    user.stock = stock
    user.price = price
    history('/products')
  }

    
  return (
    <div className="product">
      <div className="productTitleContainer">
        <h1 className="productTitle">Product</h1>
        <Link to="/newproduct">
          <button className="productAddButton">Create</button>
        </Link>
      </div>
      <div className="productTop">
          <div className="productTopLeft">
              <Chart data={productData} dataKey="Sales" title="Sales Performance"/>
          </div>
          <div className="productTopRight">
              <div className="productInfoTop">
                  <img src={img} alt="" className="productInfoImg" />
                  <span className="productName">{name}</span>
              </div>
              <div className="productInfoBottom">
                  <div className="productInfoItem">
                      <span className="productInfoKey">id:</span>
                      <span className="productInfoValue" >{id}</span>
                  </div>
                  <div className="productInfoItem">
                      <span className="productInfoKey">name:</span>
                      <span className="productInfoValue">{name}</span>
                  </div>
                  <div className="productInfoItem">
                      <span className="productInfoKey">active:</span>
                      <span className="productInfoValue">{status}</span>
                  </div>
                  <div className="productInfoItem">
                      <span className="productInfoKey">in stock:</span>
                      <span className="productInfoValue">{stock}</span>
                  </div>
                  <div className="productInfoItem">
                      <span className="productInfoKey">Price:</span>
                      <span className="productInfoValue">{price}</span>
                  </div>
              </div>
          </div>
      </div>
      <div className="productBottom">
          <form className="productForm">
              <div className="productFormLeft">
                  <label>Product Name</label>
                  <input onChange={(e) => setName(e.target.value)} type="text" placeholder={name}/>
                  <label>In Stock</label>
                  <input onChange={(e) => setStock(e.target.value)} type="text" placeholder={stock}/>
                  <label>Active</label>
                  <input onChange={(e) => setStatus(e.target.value)} type="text" placeholder={status}/>
                  <label>Price</label>
                  <input onChange={(e) => setPrice(e.target.value)} type="text" placeholder={price}/>
              </div>
              <div className="productFormRight">
                  <div className="productUpload">
                      <img src={img} alt="" className="productUploadImg" />
                      <label for="file">
                          <Publish/>
                      </label>
                      <input type="file" id="file" style={{display:"none"}} onChange={(e)=>setImg(e.target.value)} />
                  </div>
                  <button onClick={(e)=>handleUpdate(e)} className="productButton">Update</button>
              </div>
          </form>
      </div>
    </div>
  );
}
